/* section part start */
section {
    margin-top: 6.20%;
    height: fit-content;
    background-color: rgb(172, 245, 223);
    /* border: 1px solid red; */
    width: auto;
}

.bbanner {
    margin: 0% 5%;
    height: 380px;
    width: 90%;
    /* border: 1px solid green; */
}

.bbanner a img {
    margin: 20px 30px;
    width: 100%;
    height: 340px;
    box-shadow: 6px 6px 4px black;
    /* border: 2px solid yellow; */

}


a:hover .banner {
    margin: 20px 0px;
    width: 100%;
    height: 340px;
    box-shadow: 6px 6px 4px black;
}

.boximage {
    height: 260px;
    width: 260px;
    margin: 2.5%;
    border-radius: 4%;
    box-shadow: 0px 3px 6px black;
    box-sizing: border-box;
}