/* Navigation bar start */
nav {
    position: fixed;
    height:fit-content;
    width: 100%;
    background-color: rgb(251, 253, 144);
    box-shadow: 0px 3px 6px black;
    top:3.2rem;
    color: hwb(160 2% 3%);
}

.Menu_icons{
    mix-blend-mode: darken;
    height: 80px;
    width: 80px;
    padding: 14px;
}

/* Menu Bar */

.menu ul li{
    box-sizing: border-box;
    padding: 3px 28px;
    align-items: center;
}

.menu ul li:hover {
    border-radius: 50%; 
    color: gold;
    background-color: hsl(19, 87%, 50%);
    box-shadow: 0px 3px 6px hwb(0deg 12.51% 87.49%);
    cursor: pointer;
}

.menu ul li:hover .subMenu {
    display:block;
    box-sizing: border-box;
    height: 150px;
    left: 0;
    right: 0;
}

/* SubMenu Bar */

.subMenu {
    position: absolute;
    top: 102.50%;
    background-color:rgb(255 255 255);
    box-shadow: 0px 3px 6px black;
    display: none;
 
}
.subMenu ul li:hover{
    background: none;
   box-shadow: none;
}
.subMenu a:hover{
    border-radius: 50%; 
    color: gold;
    background-color: hsl(19, 87%, 50%);
    box-shadow: 0px 3px 6px hwb(0deg 12.51% 87.49%);
}
.subMenu a{
    margin: 0px 10px;
    padding: 8px 18px;
}
