footer {
    position: absolute;
    height: 45px;
    width: 100%;
    background-color: rgb(251, 253, 144);
    box-shadow: 0px 3px 6px black;
    box-sizing: border-box;
    margin: auto;
}

footer .logo
{
    padding-left: 4px;
    height: 30px;
    width: 30px;
}
.footers ul li
{
    align-items: center;
    text-transform: capitalize;
}
.footers ul li a
{
    height: 32px;
    width: 40px;
    padding: 4px;
}
.footers ul li a:hover .logo{
    height: 35px;
    width: 35px;
}
